const Statistic = ({ answersArray }) => {
    const right = answersArray.filter((item) => item.status === true);
    const wrong = answersArray.filter((item) => item.status === false);
    return (
        <div>
            <p> : {right.length}</p>
            <p> : {wrong.length}</p>
        </div>
    );
};

export default Statistic;
