import { useEffect, useLayoutEffect, useState, useContext } from "react";
import { dataContext } from "../../context";

import { changeGain, getQuestEq } from "../../logic/trainingEQ";
import { EQ, time } from "../../logic/EQ";

import Button from "../button/button";

import "./AnswerArea.sass";

const AnswerArea = ({ wavesurfer }) => {
    //TODO  state training  App,    ( )  training === false.  EQ  userSettings.
    const [selectedFreq, setSelectedFreq] = useState({
        state: false,
        value: null,
        num: null,
    });
    const [selectedDir, setSelectedDir] = useState({
        state: false,
        value: null,
    });

    const [state, dispatch] = useContext(dataContext);

    //  state.quest
    useLayoutEffect(() => {
        // , -     
        if (state.training) {
            setSelectedFreq({
                state: false,
                value: null,
                num: null,
            });
            setSelectedDir({
                state: false,
                value: null,
            });
        }
        //      ,          time
        if (!state.training && selectedDir.state && selectedFreq.state) {
            console.log("useLayoutEffect");
            console.log(state.quest);
            // checkAnswer(state.quest);
            setTimeout(() => {
                dispatch({
                    type: "getFilter",
                    setQuest: {}, //!! <--  !
                });
                setSelectedFreq({
                    state: false,
                    value: null,
                    num: null,
                });
                setSelectedDir({
                    state: false,
                    value: null,
                });
            }, time);
        }
    }, [state.quest]);

    // ,    
    useEffect(() => {
        if (selectedFreq.state && selectedDir.state && state.training) {
            checkAnswer(selectedFreq, selectedDir);
        } else if (selectedFreq.state && selectedDir.state && !state.training) {
            listenFilter();
        }
    }, [selectedFreq.state, selectedDir.state]);

    //TODO   App.js
    const listenFilter = () => {
        dispatch({
            type: "getQuest",
            setQuest: getQuestEq(
                wavesurfer.filters,
                selectedDir.value,
                selectedFreq.num
            ),
            setStateApp: time / 1000,
        });
    };

    //    state
    const handleAnswerFreq = (e) => {
        if (
            (selectedFreq.state && selectedDir.state) ||
            // !state.training ||
            !state.playing
            // ||            Object.keys(state?.quest).length === 0
        ) {
            return;
        }
        // direction,  freq === 0
        if (state.quest.freq === 0 || +e.target.dataset.freq === 0) {
            setSelectedDir({
                state: true,
                value: state.quest.dir,
            });
        }
        setSelectedFreq({
            state: true,
            value: +e.target.dataset.freq,
            num: +e.target.dataset.num,
        });
    };

    const handleAnswerDir = (e) => {
        if (
            (selectedFreq.state && selectedDir.state) ||
            // !state.training ||
            !state.playing
            // ||            Object.keys(state?.quest).length === 0
        ) {
            return;
        }
        setSelectedDir({
            state: true,
            value: +e.target.dataset.direction,
        });
    };

    const checkAnswer = (selectedFreq, selectedDir) => {
        if (
            selectedFreq?.value === state.quest.freq &&
            selectedDir?.value === state.quest.dir
        ) {
            dispatch({ type: "addAnswerInArray", payload: true });
        } else {
            dispatch({ type: "addAnswerInArray", payload: false });
        }
    };

    const showAnswer = (target, selected, answer) => {
        let s = "";
        if (selectedFreq.state && selectedDir.state) {
            if (target === answer) {
                s = "green";
            } else if (selected.value === target) {
                s = "red";
            }
        } else if (selected.state) {
            target === selected.value ? (s = "select") : (s = "");
        }
        return s;
    };

    const AnswerFreq = () => {
        const buttonsList = EQ.map((item, i) => {
            const s = showAnswer(item.f, selectedFreq, state?.quest?.freq);
            return (
                <li key={i}>
                    <button
                        onClick={handleAnswerFreq}
                        data-freq={item.f}
                        data-num={i}
                        className={`frequency ${s}`}
                        disabled={i === 0 && !state.training}
                    >
                        {i === 0 ? "none" : item.f}
                    </button>
                </li>
            );
        });
        return <ul className="frequencies">{buttonsList}</ul>;
    };

    const AnswerDir = () => {
        const directionButtons = [];
        for (let i = 1; i > -2; i -= 2) {
            let s = showAnswer(i, selectedDir, state?.quest?.dir);

            if (state?.quest?.freq === 0 && selectedFreq.state) {
                s = "";
            }

            directionButtons.push(
                <button
                    key={i}
                    onClick={handleAnswerDir}
                    data-direction={i}
                    className={`direction ${s}`}
                >
                    {i > 0 ? "" : ""}
                </button>
            );
        }
        return <div className="directions">{directionButtons}</div>;
    };

    return (
        <div className={"answers"}>
            <div>
                <AnswerDir />
                <AnswerFreq />
            </div>
        </div>
    );
};

export default AnswerArea;
